import { useCallback } from 'react';
import { 
  AppLayout,
  Box,
  Button,
  Container,
  ContentLayout,
  Grid,
  Header,
  SpaceBetween
} from '@cloudscape-design/components';
import { Link, useNavigate } from 'react-router-dom';

export type UnifiedAppLayoutProps = {
  content: JSX.Element
}

export const UnifiedAppLayout = ({content}: UnifiedAppLayoutProps) => {
  const navigate = useNavigate();

  const onChromeClick = useCallback(() => {
    navigate('/chrome');
  }, [navigate]);
  
  const onFirefoxClick = useCallback(() => {
    navigate('/firefox');
  }, [navigate]);

  const onSafariClick = useCallback(() => {
    navigate('/safari');
  }, [navigate]);

  return (
    <AppLayout
      content={
        <ContentLayout
          header={
            <Link to="/" style={{ textDecoration: 'none' }}>
              <Box
                variant="div"
                margin={{ top: "l" }}
              >
                
                <Header
                  variant="h1"
                >
                  {"Image Manager with AWS"}
                </Header>
              </Box>
            </Link>
          }
        >
          <SpaceBetween size="m">
          <Container>
            <Grid
              gridDefinition={[{ colspan: 4 }, { colspan: 4 }, { colspan: 4 }]}
            >
              <Box textAlign='center'>
                <Button variant="primary" onClick={onChromeClick}>
                  Chrome
                </Button>
              </Box>
              <Box textAlign='center'>
                <Button variant="primary" onClick={onFirefoxClick}>
                  Firefox
                </Button>
              </Box>
              <Box textAlign='center'>
                <Button variant="primary" onClick={onSafariClick}>
                  Safari
                </Button>
              </Box>
            </Grid>
          </Container>
          {content}
          </SpaceBetween>
        </ContentLayout>
      }
      navigationHide={true}
      toolsHide={true}
    />
  );
}
